<template lang="pug">
main
  br
  .container
    article.message.is-info
      .message-header
        p Informacin de envio del Comprobante Electrnico
      .message-body(v-if="track[0]")
        p Comprobante:
          strong {{ ' ' + track[0].codigo + ' ' + track[0].numero }}
        p(v-if="track[0].numeroAutorizacion") Autorizacin: {{ track[0].numeroAutorizacion }}
        //- p(v-if="track[0].fechaAutorizacion") Fecha Autorizacin: {{ track[0].fechaAutorizacion }}
        p Estado:
          strong {{ ' ' + track[0].estado }}
        p
          strong Observacin:
        p {{ track[0].observacion }}
  br
</template>
<script>
import trackService from '@/services/track'

export default {
  data: () => ({
    track: []
  }),

  created () {
    const numero = this.$route.params.numero
    trackService.electronicoById(numero)
      .then(res => {
        this.track = res
      })
  }
}
</script>
<style lang="scss" scoped>
</style>



