<template lang="pug">
  .container
    table.table.is-bordered.is-striped.is-hoverable
      thead
        tr
          th.titulo
            abbr(title='Nmero de comprobante') Factura
          th.titulo
            abbr(title='Fecha de comprobante ( aaaa/mm/dd )') Fecha
          th.titulo Total
          th.titulo Documento
          th.titulo Razn Social
          th.titulo Correo
          th.titulo Estado
          th.titulo PDF
          th.titulo XML
      tfoot
        tr
          th.titulo
            abbr(title='Nmero de comprobante') Factura
          th.titulo
            abbr(title='Fecha de comprobante ( aaaa/mm/dd )') Fecha
          th.titulo Total
          th.titulo Documento
          th.titulo Razn Social
          th.titulo Correo
          th.titulo Estado
          th.titulo PDF
          th.titulo XML
      tbody
        tr(v-for = "t in tracks")
          td(v-bind:class="t.estado | estado")
              p
                | {{ t.codigo }}
              p
                | {{` ${t.establecimiento}${t.puntoEmision}${t.secuencial}` }}
          td {{ t.fecha }}
          td.numero {{ t.total }}
          td.numero {{ t.documento }}
          td {{ t.razonSocial }}
          td {{ t.correo_electronico }}
          td
            a.button.is-info.is-outlined(
              @click="alertCustom(t.numero)",
              v-bind:class="t.estado | estado"
              ) {{ t.estado }}
          td
            a.button.is-link.is-outlined(
              v-bind:class="t.estado | estado",
              v-bind:href="`${pdf}codigo/${t.codigo}/numero/${t.numero}`",
              target="_blank"
              ) pdf
          td
            a.button.is-success.is-outlined(
              v-bind:class="t.estado | estado",
              v-bind:href="`${xml}codigo/${t.codigo}/numero/${t.numero}`",
              target="_blank"
              ) xml
</template>

<script>
import trackService from '@/services/track'

export default {
  data: () => ({
    track: []
  }),

  props: {
    tracks: { type: Array, required: true }
  },

  computed: {
    pdf () {
      return trackService.pdf()
    },
    xml () {
      return trackService.xml()
    }
  },

  methods: {
    goToEstado (numero) {
      this.$router.push({ name: 'electronico', params: { numero } })
    },
    alertCustom (numero) {
      var autorizacion = ''
      trackService.electronicoById(numero)
        .then(res => {
          this.track = res
          // console.log(this.track[0].numero)
          if (this.track[0].numeroAutorizacion) {
            autorizacion = '<strong>Autorizacin: </strong>' + this.track[0].numeroAutorizacion
          }
          this.$dialog.alert({
            title: 'Estado Electrnico',
            message:
              `
              <p>
                <strong>Comprobante:</strong> ${this.track[0].codigo} ${this.track[0].numero}
              </p>
              <p>
                ${autorizacion}
              </p>
              <p>
                <strong>Estado:</strong> ${this.track[0].estado}
              </p>
              <p>
                <strong>Observacin:</strong> ${this.track[0].observacion}
              </p>
              `,
            confirmText: 'Aceptar'
          })
        })
    }
  }
}
</script>

<style lang="scss" scoped>
td.numero {
  text-align: right;
}
th.titulo {
  text-align: center;
}
a.no_enviado {
   pointer-events: none;
   cursor: default;
}
p.no_enviado {
  color: #0066cc;
}
p.devuelta {
  color: #0066cc;
}
</style>



