<template lang="pug">
  main
    transition(name="move")
      pm-notification(v-show = "showNotification")
        p(slot = "body")
          strong No se encontraron resultados

    transition(name="move")
      pm-loader(v-show = "isLoading")

    section.section(v-show = "!isLoading")
      nav.nav.has-shadow
      .container
        h1.title Facturas Electrnicas
        label.label
          abbr(title='Fecha ( aaaa/mm/dd )') Fecha Inicial
        input.input(
          type="text",
          placeholder="2009/01/29",
          v-model='fechaInicial',
          @keyup.enter="search")
        label.label
          abbr(title='Fecha ( aaaa/mm/dd )') Fecha Final
        input.input(
          type="text",
          placeholder="2009/01/29",
          v-model='fechaFinal',
          @keyup.enter="search")
        .container
          p
            small {{ searchMessage }}
        .container
          .control
            label.radio
              input(type='radio', value='Todos', v-model='estado')
              label(for='Todos') Todos
              input(type='radio', value='NoAutorizados', v-model='estado')
              label(for='NoAutorizados') No Autorizados
              input(type='radio', value='Autorizados', v-model='estado')
              label(for='Autorizados') Autorizados
          //- .container.notification
          //-   .p {{ estado }}
        a.button.is-primary(@click="search") Buscar
        a.button.is-info(@click="autorizar") Autorizar
        a.button.is-danger &times;
      .container
        pm-detalle(v-bind:tracks = "tracks")
</template>

<script>
import trackService from '@/services/track'

import PmDetalle from '@/components/Detalle.vue'

import PmLoader from '@/components/shared/Loader.vue'
import PmNotification from '@/components/shared/Notification.vue'

export default {
  name: 'app',

  components: {
    PmDetalle,
    PmLoader,
    PmNotification
  },

  data () {
    return {
      fechaInicial: '',
      fechaFinal: '',
      estado: 'Todos',
      tracks: [],

      isLoading: false,
      showNotification: false
    }
  },

  computed: {
    searchMessage () {
      return `Encontrados: ${this.tracks.length}`
    }
  },

  watch: {
    showNotification () {
      if (this.showNotification) {
        setTimeout(() => {
          this.showNotification = false
        }, 3000)
      }
    }
  },

  methods: {
    search () {
      if (!this.fechaInicial || !this.fechaFinal) {
        console.log('No existe el rango de fechas')
        return
      }

      this.isLoading = true

      console.log('Buscando ... ')
      trackService.search(this.fechaInicial, this.fechaFinal, this.estado)
        .then(res => {
          // console.log(res)
          // console.log(res.length)
          this.showNotification = res.length === 0
          this.tracks = res
          this.isLoading = false
        })
    },
    autorizar () {
      if (!this.fechaInicial || !this.fechaFinal) {
        console.log('No existe el rango de fechas')
        return
      }

      this.tracks = []
      this.isLoading = true
      console.log('Autorizando ... ')
      trackService.autorizar(this.fechaInicial, this.fechaFinal)
        .then(res => {
          this.showNotification = res.length === 0
          // this.tracks = res
          this.isLoading = false
        })
    }
  }
}
</script>

<style lang="scss">

</style>



