import Vue from 'vue'
import VueRouter from 'vue-router'
import App from '@/App.vue'

import estado from '@/filters/estado'

import routes from '@/routes'

import Buefy from 'buefy'
import 'buefy/lib/buefy.css'

Vue.use(VueRouter)
Vue.use(estado)
Vue.use(Buefy)

const router = new VueRouter({
  routes,
  mode: 'history'
})

new Vue({
  el: '#app',
  render: h => h(App),
  router
})



