<template lang="pug">
.container
  h1.title Informacin del Contribuyente
  .card(v-if="tracks[0]")
    .content
      .p Nombre Comercial: {{ tracks[0].nombreComercial }}
      .p Razn Social: {{ tracks[0].razonSocial }}
      .p RUC: {{ tracks[0].ruc }}
      .p Direccin: {{ tracks[0].direccion }}
      .p Obligado a llevar contabilidad: {{ tracks[0].obligadoContabilidad }}
      .p(v-show="tracks[0].contribuyenteEspecial") Contribuyente Especial: {{ tracks[0].contribuyenteEspecial }}
</template>
<script>
import trackService from '@/services/track'

export default {
  data: () => ({
    tracks: [],

    isLoading: false,
    showNotification: false
  }),
  methods: {
    getDatosEmpresa () {
      this.isLoading = true
      trackService.contribuyente()
        .then(res => {
          // console.log(res)
          console.log(res.length)
          this.showNotification = res.length === 0
          this.tracks = res
          this.isLoading = false
        })
    }
  },
  beforeMount () {
    this.getDatosEmpresa()
  }
}
</script>
<style lang="scss" scoped>
</style>



