import quijoteluiService from './quijotelui'

const trackService = {}

trackService.search = function (fechaInicio, fechaFin, estado) {
  // console.log(quijoteluiService)
  const fechaReporte = `/reporte_factura_estado/fechaInicio/${fechaInicio}/fechaFin/${fechaFin}/estado/${estado}`
  return quijoteluiService.get(fechaReporte)
    .then(res => {
      console.log(`Url Base: ${quijoteluiService._baseUrl}`)
      return res.data
    })
}

trackService.autorizar = function (fechaInicio, fechaFin) {
  const fechasAutorizar = `/facturaAutorizarFechas/fechaInicio/${fechaInicio}/fechaFin/${fechaFin}`
  return quijoteluiService.get(fechasAutorizar)
    .then(res => {
      return res.data
    })
}

trackService.contribuyente = function () {
  return quijoteluiService.get(`/contribuyentes`)
    .then(res => res.data)
}

trackService.pdf = function () {
  const comprobante = `/pdf/`
  return `${quijoteluiService._baseUrl}${comprobante}`
}

trackService.xml = function () {
  const comprobante = `/xml/`
  return `${quijoteluiService._baseUrl}${comprobante}`
}

trackService.electronicoById = function (numero) {
  const comprobante = `/electronico/codigo/FAC/numero/${numero}`
  return quijoteluiService.get(comprobante)
    .then(res => {
      return res.data
    })
}

export default trackService



